import { Component, OnInit, Output, Inject } from '@angular/core';
import { MatBottomSheetRef, MAT_BOTTOM_SHEET_DATA } from '@angular/material';

@Component({
  selector: 'app-colors-bottom-sheet',
  templateUrl: './colors-bottom-sheet.component.html',
  styleUrls: ['./colors-bottom-sheet.component.scss']
})
export class ColorsBottomSheetComponent {

  @Output() changeColor;

  constructor(
    private bottomSheetRef: MatBottomSheetRef,
    @Inject(MAT_BOTTOM_SHEET_DATA) public data: any
  ) { }

  onChangePencilColor(color) {
    this.data.changeColor(color);
  }
}
