import { Component, OnInit, OnDestroy, Sanitizer, ElementRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ColoringService } from '../coloring.service';
import { SvgService } from '../svg.service';
import { MatBottomSheet } from '@angular/material';
import { ColorsBottomSheetComponent } from '../colors-bottom-sheet/colors-bottom-sheet.component';

@Component({
  selector: 'app-list-item',
  templateUrl: './list-item.component.html',
  styleUrls: ['./list-item.component.scss']
})
export class ListItemComponent implements OnInit, OnDestroy {
  item;
  svg;
  pencilColor = '#f44242';
  maxWidth: number;
  maxHeight: number;
  private alias: any;
  private sub: any;
  @ViewChild('coloring') coloring: ElementRef;

  constructor(
    private coloringService: ColoringService,
    private svgService: SvgService,
    private route: ActivatedRoute,
    private bottomSheet: MatBottomSheet,
    private el: ElementRef
  ) { }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.alias = 'coloring-1'; // params['alias'];
      this.coloringService.getList()
        .subscribe((list) => {
          this.item = list.find(item => item.alias === this.alias);

          const svgPath = `assets/images/coloring/${this.item.id}.svg`;
          this.svgService.load2(svgPath)
            .subscribe(svg => {
              this.svg = svg;
               const data = this.coloring.nativeElement.getBoundingClientRect();
               this.maxWidth = data.width;
               this.maxHeight = data.height;
            });
        });
   });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  onChangePencilColor(color) {
    this.pencilColor = color;
  }

  openBottomSheet(): void {
    this.bottomSheet.open(ColorsBottomSheetComponent, {
      data: {
        changeColor: color => this.onChangePencilColor(color)
      }
    });
  }
}
