import { Component, OnInit, Input, OnChanges, HostListener, Output, EventEmitter } from '@angular/core';
import * as chroma from 'chroma-js';

@Component({
  selector: 'app-colors-palette',
  templateUrl: './colors-palette.component.html',
  styleUrls: ['./colors-palette.component.scss']
})
export class ColorsPaletteComponent implements OnChanges {

  @Output() changeColor = new EventEmitter;
  @Input() color;
  colors = [];
  colorIndex = 60;
  isMousedown = false;

  constructor() { }

  @HostListener('window:mouseup')
  onWindowMouseup() {
    this.isMousedown = false;
  }

  ngOnChanges() {
    this.colors = [
      ...chroma.scale(['white', this.color]).colors(60),
      ...chroma.scale([this.color, 'black']).colors(60).slice(1)
    ];

    this.changeColor.emit(this.colors[this.colorIndex]);
  }

  onMousedown(e) {
    this.isMousedown = true;
    this.updateCurrentColor(e.target);
  }

  onMousemove(e) {
    if (!this.isMousedown) {
      return;
    }

    this.updateCurrentColor(e.target);
  }

  updateCurrentColor(element) {
    while (element) {
      if (element.dataset.color) {
        this.colorIndex = this.colors.indexOf(element.dataset.color);
        this.changeColor.emit(element.dataset.color);
        break;
      }
      element = element.parentElement;
    }
  }
}
