import { Component, OnInit, ViewChild, ElementRef, Input, NgZone } from '@angular/core';
import * as d3 from 'd3';
import * as Hammer from 'hammerjs';

const throttle = (func, limit) => {
  let inThrottle;
  return (...args) => {
    if (!inThrottle) {
      func.apply(this, args);
      inThrottle = true;
      setTimeout(() => inThrottle = false, limit);
    }
  };
};

@Component({
  selector: 'app-svg',
  templateUrl: './svg.component.html',
  styleUrls: ['./svg.component.scss']
})
export class SvgComponent implements OnInit {

  @Input() svg;
  @Input() color;
  @Input() maxWidth;
  @Input() maxHeight;
  @ViewChild('myTemplate') myTemplate: ElementRef;
  buttons = [];
  groups = [];
  group = null;
  e = {test: 1};
  width;
  height;
  deltaX = 0;
  deltaY = 0;

  constructor(
    private el: ElementRef,
    private zone: NgZone
  ) { }

  ngOnInit() {
    this.myTemplate.nativeElement.innerHTML = this.svg;
    this.groups = Array.from(this.myTemplate.nativeElement.querySelectorAll('svg>g'));
    this.color = 'red';
    this.group = null;

    let width = this.width = this.maxWidth;
    let height = this.height = this.maxHeight;
    let deltaX = 0;
    let deltaY = 0;

    this.width = this.maxWidth;
    this.height = this.maxHeight;

    // this.width = 1517;
    // this.height = 2266;
    // this.deltaX = -816;
    // this.deltaY = -430;

    const svg = d3.select('#coloring svg');
    const svg1 = this.myTemplate.nativeElement.querySelector('svg');

    svg1.style.transform = `translate(${this.deltaX}px, ${this.deltaY}px)`;
    // svg1.style.height = `${this.maxHeight}px`;
    // svg1.style.width = `${this.maxWidth}px`;

    this.zone.runOutsideAngular(() => {
      const hammer = new Hammer(this.el.nativeElement);

      let requestID;

      const requestAnimationFrame = window.requestAnimationFrame || (<any>window).mozRequestAnimationFrame ||
                            window.webkitRequestAnimationFrame || (<any>window).msRequestAnimationFrame;

      const cancelAnimationFrame = window.cancelAnimationFrame || (<any>window).mozCancelAnimationFrame;


      function step(timestamp) {
        this.zone.runOutsideAngular(() => {
          console.log('requestAnimationFrame');
          svg1.style.height = `${height}px`;
          svg1.style.width = `${width}px`;
          svg1.style.transform = `translate(${deltaX}px, ${deltaY}px)`;

          requestID = requestAnimationFrame(step.bind(this));
        });
      }

      hammer.on('pinchstart', () => {
        this.zone.runOutsideAngular(() => {
        requestID = requestAnimationFrame(step.bind(this));
        });
      });
      hammer.on('pinchmove', throttle((e) => {
        this.zone.runOutsideAngular(() => {
        console.log('pinch');
        this.e = e;
        const offset = Math.floor((e.scale - 1) * 200);

        console.log('touchmove:offset ', offset);
        height = this.height + offset;
        const viewBox = svg.attr('viewBox').split(' ');
        const [w, h] = viewBox.slice(2);
        const ratio = +h / +w;
        width = Math.floor(height / ratio);
        deltaX = this.deltaX + e.deltaX;
        deltaY = this.deltaY + e.deltaY;
        });
        // svg
        //   .attr('width', width)
        //   .attr('height', height)
        //   .attr('transform', `translate(${deltaX}, ${deltaY})`);
      }, 100));

      // let wheelTimerId;
      // svg1.addEventListener('wheel', (e) => {
      //   if (!requestID) {
      //     this.zone.runOutsideAngular(() => {
      //       requestID = requestAnimationFrame(step.bind(this));
      //       });
      //   }

      //   const offset = e.deltaY;

      //   console.log('touchmove:offset ', offset);
      //   height = height - offset;
      //   const viewBox = svg.attr('viewBox').split(' ');
      //   const [w, h] = viewBox.slice(2);
      //   const ratio = +h / +w;
      //   width = Math.floor(height / ratio);
      //   deltaX = this.deltaX + e.deltaX;
      //   deltaY = this.deltaY + e.deltaY;

      //   clearTimeout(wheelTimerId);
      //   wheelTimerId = setTimeout(() => {
      //     this.width = width;
      //     this.height = height;
      //     this.deltaX = deltaX;
      //     this.deltaY = deltaY;

      //     cancelAnimationFrame(requestID);
      //     requestID = null;
      //   }, 1000);
      // });

      hammer.on('pinchend', () => {
          this.width = width;
          this.height = height;
          this.deltaX = deltaX;
          this.deltaY = deltaY;

          cancelAnimationFrame(requestID);
          requestID = null;
      });
      hammer.get('pinch').set({ enable: true });
      hammer.get('rotate').set({ enable: true });


    let defs = svg1.querySelector('svg>defs');
    if (!defs) {
        defs = document.createElementNS('http://www.w3.org/2000/svg', 'defs');
        svg1.append(defs);
    }

    this.groups.forEach((g, index) => {
        const clipPath = document.createElementNS('http://www.w3.org/2000/svg', 'clipPath');
        clipPath.id = `clipPath${index}`;

        const paths = Array.from(g.querySelectorAll('path'));
        paths.forEach((p: any) => clipPath.append(p.cloneNode(true)));

        defs.append(clipPath);
        g.style.clipPath = `url(${window.location.href}#${clipPath.id})`;

    });

    const line = d3.line();
    svg
      .attr('width', this.width)
      .attr('height', this.height)
      .attr('preserveAspectRatio', 'xMidYMid meet');

    const drawObj = {
      isDown: false,
      dataPoints: [],
      currentPath: null,
      color: 0
    };

    // svg.on('click', () => {
    //   const viewBox = svg.attr('viewBox').split(' ');
    //   const [w, h] = viewBox.slice(2);
    //   console.log('click: ', d3.event.x, d3.event.y, svg);
    //   console.log('width: ', w, this.width, d3.event.x * (+w / this.width) - 20);
    //   console.log('height: ', h, this.height, d3.event.y * (+h / this.height) - 40);
    //   const ratio = +h / this.height;
    //   svg.append('circle')
    //     .style('stroke', 'gray')
    //     .style('fill', 'white')
    //     .attr('r', 40)
    //     .attr('cx', (d3.event.x - this.deltaX - (this.width - +w / ratio) / 2) * ratio)
    //     .attr('cy', (d3.event.y - this.deltaY - 90 - (this.height - +h / ratio) / 2) * ratio);
    // });
    svg.on('touchstart', () => {
      if (d3.event.touches.length !== 1) {
        return;
      }

      drawObj.isDown = true;

      this.group = null;
      let el = d3.event.touches[0].target;
      const event = d3.event;
// debugger;
      while (el) {
        if (el.tagName === 'g') {
            this.group = d3.select(el);
        }
        el = el.parentElement;
      }

      if (this.group) {

      const viewBox = svg.attr('viewBox').split(' ');
      const [w, h] = viewBox.slice(2);
      const ratio = +h / this.height;
      const x = (d3.event.touches[0].pageX - this.deltaX - (this.width - +w / ratio) / 2) * ratio;
      const y = (d3.event.touches[0].pageY - this.deltaY - 90 - (this.height - +h / ratio) / 2) * ratio;

        drawObj.dataPoints.push(
            [x, y],
            [x + 50, y + 50]
          );
          if (!drawObj.currentPath) {
            drawObj.currentPath = this.group.append('path')
              .attr('class', 'currentPath')
              .style('stroke-width', 50)
              .style('stroke', this.color)
              .style('fill', 'none');
          }
          drawObj.currentPath
            .datum(drawObj.dataPoints)
            .attr('d', line);
      }

    });
    svg.on('touchmove', throttle((e) => {
      if (d3.event.touches.length !== 1) {
        return;
      }

      if (drawObj.isDown && this.group) {
        const viewBox = svg.attr('viewBox').split(' ');
        const [w, h] = viewBox.slice(2);
        const ratio = +h / this.height;
        const x = (d3.event.targetTouches[0].pageX - this.deltaX - (this.width - +w / ratio) / 2) * ratio;
        const y = (d3.event.targetTouches[0].pageY - this.deltaY - 90 - (this.height - +h / ratio) / 2) * ratio;

        drawObj.dataPoints.push(
          [x, y]
        );
        if (!drawObj.currentPath) {
          drawObj.currentPath = this.group.append('path')
            .attr('class', 'currentPath')
            .style('stroke-width', 100)
            .style('stroke', this.color)
            .style('fill', 'none');
        }
        drawObj.currentPath
          .datum(drawObj.dataPoints)
          .attr('d', line);
      }
    }, 100));
    svg.on('touchend', () => {
      drawObj.isDown = false;
      drawObj.currentPath.attr('class', 'oldPath');
      drawObj.dataPoints = [];
      drawObj.currentPath = null;
      if (++drawObj.color > 19) {
        drawObj.color = 0;
      }
    });

   });
  }

}
