import { Component, OnInit, HostListener, Output, EventEmitter } from '@angular/core';
import * as chroma from 'chroma-js';


@Component({
  selector: 'app-colors',
  templateUrl: './colors.component.html',
  styleUrls: ['./colors.component.scss']
})
export class ColorsComponent implements OnInit {

  colors = [];
  currentColor;
  pencilColor;
  isMousedown = false;
  @Output() changeColor = new EventEmitter;

  constructor() { }

  @HostListener('window:mouseup')
  onWindowMouseup() {
    this.isMousedown = false;
  }

  ngOnInit() {
    this.colors = [
      ...chroma.scale(['#f44242', '#f1f442']).colors(20),
      ...chroma.scale(['#f1f442', '#47f441']).colors(20).slice(1),
      ...chroma.scale(['#47f441', '#41f4e2']).colors(20).slice(1),
      ...chroma.scale(['#41f4e2', '#4341f4']).colors(20).slice(1),
      ...chroma.scale(['#4341f4', '#e23ff4']).colors(20).slice(1),
      ...chroma.scale(['#e23ff4', '#f43f3f']).colors(20).slice(1)
    ];
    this.currentColor = this.colors[0];
    this.setPencilColor(this.currentColor);
  }

  onMousedown(e) {
    this.isMousedown = true;
    this.updateCurrentColor(e.target);
  }

  onMousemove(e) {
    if (!this.isMousedown) {
      return;
    }

    this.updateCurrentColor(e.target);
  }

  updateCurrentColor(element) {
    while (element) {
      if (element.dataset.color) {
        this.currentColor = element.dataset.color;
        break;
      }
      element = element.parentElement;
    }
  }

  setPencilColor(color) {
    this.pencilColor = color;
    this.changeColor.emit(this.pencilColor);
  }
}
